<template>
  <div class="tabbar">
    <van-tabbar route v-model="active" active-color="#ee0a24" inactive-color="#000">
      <van-tabbar-item to="/home" icon="wap-home-o">首页</van-tabbar-item>
      <van-tabbar-item to="/classfiction" icon="qr">分类页</van-tabbar-item>
      <van-tabbar-item v-if="cartList" to="/cart" icon="shopping-cart-o"  :badge="cartList">购物车</van-tabbar-item>
      <van-tabbar-item v-else to="/cart" icon="shopping-cart-o">购物车</van-tabbar-item>
      <van-tabbar-item to="/my" icon="user-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { getStorage } from '@/utils/storage'
export default {
  name: 'tabbarIndex',
  data () {
    return {
      active: 0
    }
  },
  computed: {
    cartList () {
      if (!getStorage('check')) return false
      if (!getStorage('check').length) {
        return false
      } else {
        return this.$store.state.cart.cartList.length || getStorage('check').length
      }
    }
  }
}
</script>

<style lang="less" scoped>
 .van-tabbar {
  border-top: 1px solid lightgray;
}
</style>
